
import React from 'react';
import './App.css';
import image from './images/timeclock.jpg'

class Clock extends React.Component {
  constructor() {
    super();
    var Today=new Date();
    var hours=Today.getHours()
    var min=Today.getMinutes()
    var seconds=Today.getSeconds()
    this.state = {
      second: seconds,
      minute:min,
      hour:hours,
    };
  }

  ChangeTime=()=>
  {
    var Today=new Date();
    var hours=Today.getHours()
    var min=Today.getMinutes()
    var seconds=Today.getSeconds()
    this.setState({ second: seconds,
      minute:min,
      hour:hours,})
  }

  render() {
    setInterval(this.ChangeTime,1000)
    return (
      <div>
        <h2>DIGITAL CLOCK REACT</h2>
        <img src={image} alt="not found" className="image1"></img>
        <div className="main">

          
        <div>
            <h1>{this.state.hour}:</h1><h1>{this.state.minute}:</h1><h1>{this.state.second}</h1>
        </div>

        
        </div>
      </div>
    );
  }
}

export default Clock;
